import MusicPlayer from './musicplayer';

//
const components = [MusicPlayer];

//install
const install = (Vue)=> {
    //
    if(install.installed)return;
    install.installed  = true;
    //
    components.map(component =>{
        Vue.use(component);
    });
    //Vue
    if(typeof  window !=='undefined' && window.Vue){
        install(window.Vue);
    }
};

export  default {
    //installVue.use
    install,
    ...components
}
